.keyboard {
    width: 768px;
    height: 294px;
    background-color: #4cbefa;
    -webkit-border-radius: 10px;
    border-radius: 10px;
    margin: 0px auto 170px auto;
    border-top: 1px solid #7acffb;
    border-bottom: 1px solid #49a9db;
    position: relative;

    .kBack {
        width: 72px!important;
        margin-right: 0!important;
        border-top: 1px solid #b8e0fc;

        &.active {
            background-color: #78a6c5;
        }
    }

    .kTab {
        border-top: 1px solid #b8e0fc;
        width: 72px!important;
    }

    .kSlash {
        border-top: 1px solid #b8e0fc;
    }

    .kCaps {
        border-top: 1px solid #b8e0fc;
        width: 87px!important;
    }

    .kEnter {
        border-top: 1px solid #b8e0fc;
        width: 85px!important;
    }

    .kShift {
        border-top: 1px solid #b8e0fc;
        width: 114px!important;
    }

    .kShiftR {
        border-top: 1px solid #b8e0fc;
        width: 112px!important;
        margin-right: 0!important;
    }

    .keyboard-key-space {
        margin-left: 169px!important;
    }

    .kSpace {
        width: 409px!important;
        margin: 1px 0 1px 0!important;
        float: none!important;
        border-top: 1px solid #b8e0fc;

        &.active {
            background-color: #78a6c5;
        }
    }

    .keyboard-container {
        display: inline-block;
        height: auto!important;
        vertical-align: middle;
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
    }

    .keyboard-keyset {
        padding: 6px;
    }

    .row {
        margin-right: 0;
        margin-left: 0;
        overflow: hidden;
        clear: both;
        padding-bottom: 4px;
        text-transform: uppercase;

        &>* {
            width: auto;
            padding-left: 0;
            padding-right: 0;
        }

        &:after:before {
            clear: both;
            display: table;
            content: " ";
        }

        i {
            display: block;
            font-style: normal;
            float: left;
            text-align: right!important;
            margin-left: 2px;
            border: 2px solid transparent;
            -webkit-border-radius: 6px;
            border-radius: 6px;

            &:first-child {
                margin-left: 0;
            }

            &.cHover {
                border: 2px solid #fff;
            }
        }

        div {
            width: 46px;
            height: 46px;
            font-size: 16px;
            line-height: 46px;
            text-align: center;
            background-color: #9ad3fa;
            border-bottom: 1px solid #43a7dc;
            color: #88a9b4;
            cursor: default;
            -webkit-border-radius: 5px;
            border-radius: 6px;
            transition: 0.1s;

            &.c0 {
                background-color: #9ad3fa;
                border-top: 1px solid #b8e0fc;

                &.active {
                    background-color: #77a5c4;
                }
            }

            &.c1 {
                border-top: 1px solid #a1ebc4;
                background-color: #78e2aa;

                &.active {
                    background-color: #5db085;
                }
            }

            &.c2 {
                border-top: 1px solid #92ebff;
                background-color: #63e3ff;

                &.active {
                    background-color: #4db3c9;
                }
            }

            &.c3 {
                background-color: #f69ec4;
                border-top: 1px solid #f9bbd6;

                &.active {
                    background-color: #c57e9d;
                }
            }

            &.c4 {
                background-color: #ffc07e;
                border-top: 1px solid #ffd3a5;

                &.active {
                    background-color: #d7a26b;
                }
            }

            &.c5 {
                background-color: #ffef7e;
                border-top: 1px solid #fff4a5;

                &.active {
                    background-color: #d7ca69;
                }
            }

            span {
                width: 100%;
                font-size: 16px;
                line-height: 1;
                text-align: center;
                cursor: default;
                color: #999;
            }
        }
    }
}

.rate-container {
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.rating {
    display: flex;
    width: 100%;
    justify-content: left;
    overflow: hidden;
    flex-direction: row-reverse;
    position: relative;
}

.rating-0 {
    filter: grayscale(100%);
}

.rating > input {
    display: none;
}

.rating > label {
    cursor: pointer;
    width: 30px;
    height: 30px;
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='126.729' height='126.73'%3e%3cpath fill='%23e3e3e3' d='M121.215 44.212l-34.899-3.3c-2.2-.2-4.101-1.6-5-3.7l-12.5-30.3c-2-5-9.101-5-11.101 0l-12.4 30.3c-.8 2.1-2.8 3.5-5 3.7l-34.9 3.3c-5.2.5-7.3 7-3.4 10.5l26.3 23.1c1.7 1.5 2.4 3.7 1.9 5.9l-7.9 32.399c-1.2 5.101 4.3 9.3 8.9 6.601l29.1-17.101c1.9-1.1 4.2-1.1 6.1 0l29.101 17.101c4.6 2.699 10.1-1.4 8.899-6.601l-7.8-32.399c-.5-2.2.2-4.4 1.9-5.9l26.3-23.1c3.8-3.5 1.6-10-3.6-10.5z'/%3e%3c/svg%3e");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 76%;
    transition: .3s;
}

.rating > input:checked ~ label,
.rating > input:checked ~ label ~ label {
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='126.729' height='126.73'%3e%3cpath fill='%23fcd93a' d='M121.215 44.212l-34.899-3.3c-2.2-.2-4.101-1.6-5-3.7l-12.5-30.3c-2-5-9.101-5-11.101 0l-12.4 30.3c-.8 2.1-2.8 3.5-5 3.7l-34.9 3.3c-5.2.5-7.3 7-3.4 10.5l26.3 23.1c1.7 1.5 2.4 3.7 1.9 5.9l-7.9 32.399c-1.2 5.101 4.3 9.3 8.9 6.601l29.1-17.101c1.9-1.1 4.2-1.1 6.1 0l29.101 17.101c4.6 2.699 10.1-1.4 8.899-6.601l-7.8-32.399c-.5-2.2.2-4.4 1.9-5.9l26.3-23.1c3.8-3.5 1.6-10-3.6-10.5z'/%3e%3c/svg%3e");
}


.rating > input:not(:checked) ~ label:hover,
.rating > input:not(:checked) ~ label:hover ~ label {
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' width='126.729' height='126.73'%3e%3cpath fill='%23d8b11e' d='M121.215 44.212l-34.899-3.3c-2.2-.2-4.101-1.6-5-3.7l-12.5-30.3c-2-5-9.101-5-11.101 0l-12.4 30.3c-.8 2.1-2.8 3.5-5 3.7l-34.9 3.3c-5.2.5-7.3 7-3.4 10.5l26.3 23.1c1.7 1.5 2.4 3.7 1.9 5.9l-7.9 32.399c-1.2 5.101 4.3 9.3 8.9 6.601l29.1-17.101c1.9-1.1 4.2-1.1 6.1 0l29.101 17.101c4.6 2.699 10.1-1.4 8.899-6.601l-7.8-32.399c-.5-2.2.2-4.4 1.9-5.9l26.3-23.1c3.8-3.5 1.6-10-3.6-10.5z'/%3e%3c/svg%3e");
}

#rating-1:checked ~ .emoji-wrapper > .emoji { transform: translateY(-100px); }
#rating-2:checked ~ .emoji-wrapper > .emoji { transform: translateY(-200px); }
#rating-3:checked ~ .emoji-wrapper > .emoji { transform: translateY(-300px); }
#rating-4:checked ~ .emoji-wrapper > .emoji { transform: translateY(-400px); }
#rating-5:checked ~ .emoji-wrapper > .emoji { transform: translateY(-500px); }

.feedback {
    max-width: 130px;
    background-color: #fff;
    width: 100%;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
}

.traffic-light {
    border-radius: 100%;
    padding: 0;
    height: 12px;
    width: 12px;
    border: 1px solid rgba(0, 0, 0, 0.06);
    box-sizing: border-box;
    margin-right: 3.5px;
    background-color: #ddd;
    position: relative;
    outline: none;

    &.red {
        background-color: #bf4942;
    }

    &.yellow {
        background-color: #ffbd2e;
    }

    &.green {
        background-color: #1d9730;
    }

    &.disabled {
        background-color: #ddd;
    }
}



.game-block {
    .hidden-text {
        height: 100%;
        font-size: 25px;
        background-color: #f3f3f3;
        border-radius: 0.25rem;
        p {
        }
    }

    .game-text {
        font-size: 20px;
        span {
            &.active {
                color: #ffffff;
                background-color: #0d6efd;
                border-radius: 6px;
                padding: 3px;
                transition: 0.3s;
                box-shadow: 0 0 1px rgb(0 0 0 / 0%);
                -webkit-transition-duration: 0.3s;
                transition-duration: 0.3s;
                -webkit-transition-property: transform;
                transition-property: transform;
                -webkit-transition-timing-function: ease-out;
                transition-timing-function: ease-out;
                transform: translateY(-2px);
                display: inline-block;
            }
            &.error {
                color: #ffffff;
                background-color: #fd0000;
                border-radius: 6px;
                padding: 3px;
            }
        }
    }

    .game-stats {
        font-size: 13px;
        font-weight: 500;

        i {
            font-size: 20px;
        }

        .speed, .mistake, .percent-mistake, .time {
            font-size: 25px;
        }
    }
}

.form-control {
    &.error:focus {
        border-color: #fe8686;
        box-shadow: 0 0 0 0.25rem rgb(250 11 11 / 25%);
    }
}

input[name="sortRecords"]:checked+label {
    color: rgba(var(--bs-dark-rgb),var(--bs-text-opacity))!important;
}
